/* stylelint-disable */
.products {
  margin-top: -1px;
}
.products__top {
  padding: 60px 30px;
  align-items: center;
  justify-content: space-between;
}
.products__top::before {
  content: "";
  display: block;
  width: 225px;
  height: 156px;
  background-image: url("../img/products-decor-1.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 768px) {
  .products__top::before {
    width: 100px;
    height: 70px;
  }
}
.products__top::after {
  content: "";
  display: block;
  width: 225px;
  height: 156px;
  background-image: url("../img/products-decor-2.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 768px) {
  .products__top::after {
    width: 100px;
    height: 70px;
  }
}
.products__title {
  margin: 0 auto;
  max-width: 785px;
  font-weight: 400;
  font-size: 65px;
  line-height: 73px;
  letter-spacing: 0.5px;
  text-align: center;
  text-transform: uppercase;
  color: var(--dark-color);
}
@media (max-width: 1024px) {
  .products__title {
    padding: 40px 0;
  }
}
@media (max-width: 768px) {
  .products__title {
    padding: 20px;
    font-size: 45px;
    line-height: 50px;
  }
}
.products__content {
  position: relative;
  margin-top: -1px;
  padding-top: 20px;
}
.products__decorative {
  position: absolute;
  left: 50%;
  top: 20px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: calc(100% - 40px);
  transform: translateX(-50%);
}
.products__decorative svg:nth-child(2) {
  transform: translateX(-15px);
}
.products__decorative svg:nth-child(3) {
  transform: translateX(15px);
}
@media (max-width: 1024px) {
  .products__decorative {
    display: none;
  }
}
@media (max-width: 1024px) {
  .products__item {
    width: calc(50% - 13px);
  }
}
@media (max-width: 768px) {
  .products__item {
    width: 100%;
  }
}
.products__ticker {
  margin-top: -1px;
}
.products__phrase {
  padding: 23px 0;
  font-weight: 400;
  font-size: 17px;
  line-height: 26px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: var(--dark-color);
}
.products__list {
  display: flex;
  flex-wrap: wrap;
  padding: 0 20px;
  padding-bottom: 20px;
  gap: 26px;
}
.products__cover {
  padding: 20px;
}
.products__cover img {
  width: 100%;
  object-fit: cover;
}